@use "base/variables" as var;
@use "base/mixins" as mix;

.table {
  width: 100%;
  border-collapse: collapse;

  &__thead {
    background-color: var.$gris-oscuro;
    color: var.$blanco;
  }

  &__th {
    padding: 1rem;
    text-align: left;
  }

  &__tr {
    &:nth-child(even) {
      background-color: var.$gris-claro;
    }
  }

  &__td {
    padding: 1.5rem 1rem;

    &--acciones {
      @extend .table__td;
      display: flex;
      justify-content: flex-end;
      gap: 2rem;
      align-items: center;
    }
  }

  &__accion {
    all: unset;
    text-transform: uppercase;
    font-size: 1.4rem;
    font-weight: var.$bold;

    &--editar {
      color: var.$azul;
    }

    &--eliminar {
      color: var.$rojo;
    }

    &:hover {
      cursor: pointer;
    }
  }
}
