@use "base/variables" as var;

.acciones {
  margin-top: 3rem;
  display: flex;
  justify-content: space-between;

  &__enlace {
    color: var.$negro;
    font-size: 1.4rem;
    font-weight: var.$delgada;
  }

  &--center {
    @extend .acciones;
    justify-content: center;
  }
}
